"use client";

import React from "react";
import levels from "../module/data/modules"; // Datos de niveles y módulos
import ModuleCard from "../../progress/components/ModuleCard";
import UserProgress from "../../progress/components/UserProgress"; // Importar UserProgress

const ProgressPage = () => {
  return (
    <div className="p-6">
      {/* Barra de progreso de usuario */}
      <UserProgress
        username="Camila Gómez"
        levels={[
          {
            id: 1,
            label: "Principiante",
            completedIcon: "/assets/level-1-completed.png",
            lockedIcon: undefined, // No hay estado bloqueado en el primer nivel
            state: "completed",
          },
          {
            id: 2,
            label: "Intermedio",
            completedIcon: "/assets/level-2-completed.png",
            lockedIcon: "/assets/level-2-locked.png",
            state: "in-progress",
          },
          {
            id: 3,
            label: "Avanzado",
            completedIcon: "/assets/level-3-completed.png",
            lockedIcon: "/assets/level-3-locked.png",
            state: "locked",
          },
        ]}
      />


      {/* Tarjetas de módulos */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {levels.map((level) => (
          <div key={level.id} className="flex flex-col gap-4 card-level">
            <h2 className="level-title">{level.title}</h2>
            <p className="level-subtitle">{level.subtitle}</p>
            <div className="flex flex-col gap-2">
              {level.modules.map((module) => (
                <ModuleCard
                  key={module.id}
                  title={module.title}
                  status={module.status}
                  href={
                    module.status !== "locked"
                      ? `/dashboard/progress/module/${module.id}`
                      : undefined
                  }
                />
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProgressPage;
